.image-gallery {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 30px;
}

.img-box {
  position: relative;
  display: inline-block;
  text-align: center;
}

.img-box img {
  width: 50px;
  height: 50px;
  object-fit: cover;
  border-radius: 15%;
}
.img-box:hover img {
  transform: scale(1.1); 
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); 
}

.img-box span {
  position: absolute;
  top: 70%;
  left: 20%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 22px;
  font-weight: bold;
  text-shadow: 1px 1px 2px black; 
  pointer-events: none; 
}
.shipping-header {
  font-family: 'Merriweather', serif; 
  font-weight: 700; 
  font-size: 2.5rem;
  color: #333;
  margin-bottom: 20px; 
  text-align: center; 
}

/* Animation.css or ImageGallery.css */
@keyframes slide-up {
  0% {
      transform: translateY(20px); /* Start slightly below */
      opacity: 0; /* Start invisible */
  }
  100% {
      transform: translateY(0); /* End at original position */
      opacity: 1; /* End fully visible */
  }
}

.slide-up {
  animation: slide-up 0.5s ease forwards; /* Animation properties */
}

.fade-in {
  opacity: 1;
  transition: opacity 0.5s ease;
}

.fade-out {
  opacity: 0;
  transition: opacity 0.5s ease;
}

